/**@jsx jsx */
import { jsx, Flex, Link } from "theme-ui";
import { graphql } from "gatsby";

import Layout from "../components/layout";
import BlogPosts from "../components/blog-posts";

export const SubHead = ({ title, linkTitle, url}) => {
    return(
        <Flex sx={{
            alignItems: 'center',
            justifyItems: 'center',
            justifyContent: 'space-between',
            borderBottom: '1.5px solid',
            borderColor: 'primary',
            mb: '2rem'
        }}>
            <h2>{title}</h2>
            {
                linkTitle !== "" 
                &&
                <div>
                    <Link href={url} sx={{
                        textDecoration: 'none',
                        ":hover": {
                            color: 'secondary',
                            textDecoration: 'underline'
                        }
                    }}>
                        {linkTitle}
                    </Link>
                </div> 
            }
        </Flex>
    )
}

export default ({ data }) => {
    const posts = data.posts.edges
    return(
        <Layout>
            <section sx={{ mb: ['4rem', '8rem', '8rem', '16rem']}}>
                <div sx={{ 
                    fontSize: ['1.8rem', '2.5rem', '2.5rem', '3rem'],
                    fontWeight: 700
                }}>Hi.</div>
                <p sx={{ 
                    fontSize: ['1rem', '1.5rem', '1.5rem', '1.5rem'],
                    mt: '0.5rem'
                }}>
                    I'm Wildan, currently i'm Backend Engineer at <Link href="https://tlab.co.id" target="_blank">T'lab</Link> Yogyakarta ID. 
                    I build software product using Golang, Laravel and Javascript.
                </p>
            </section>

            <SubHead title="Latest Posts" linkTitle="Read all posts" url="blog"/>
            
            <BlogPosts data={posts}/>
        </Layout>
    )
}

export const query = graphql`
query {
    posts: allMdx(sort: {
        fields: frontmatter___date,
        order: DESC
    }, limit: 8) {
        edges {
            node {
                id
                frontmatter {
                    title
                    date(formatString: "YYYY / MM / DD")
                }
                fields {
                    slug
                }
            }
        }
    }
}
`